.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  overflow: auto;
  background-color: #e9e9e9;
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 20px;
  background-color: #e9e9e9;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}

.main-header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 1200px;
  position: relative;
}

.main-wgo-image {
  width: 30px;
  height: 47px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.main-header-buttons {
  margin-left: auto;
  display: flex;
  gap: 15px;
}

.main-logout-button,
.main-mypage-button {
  border-radius: 5px;
  padding: 10px 33px;
  cursor: pointer;
  border: none;
  font-size: 14px;
}

.main-logout-button {
  background-color: white;
  color: black;
}

.main-mypage-button {
  background-color: black;
  color: white;
  margin-right: 60px;
}

.main-content {
  width: 100%;
  max-width: 1250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20px;
  margin-top: 80px;
}

.main-title-image {
  margin: 35px auto;
  width: 900px;
  display: block;
}

.main-content-container {
  display: flex;
  width: 100%;
  gap: 20px;
  margin-top: -70px;
}

.main-left-box {
  flex: 5.5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-right: 100px;
}

.main-right-box {
  flex: 3.8;
  display: flex;
  flex-direction: column;
  margin-top: 80px;
  margin-left: 15px;
}

.main-left-image {
  width: 600px;
  height: 360px;
  border-radius: 10px;
  object-fit: cover;
  cursor: pointer;
}

.main-left-text-main {
  font-weight: bold;
  text-align: left;
  width: 100%;
  font-size: 17px;
  margin-bottom: 5px;
  margin-top: 110px;
  cursor: pointer;
}

.main-left-text {
  margin-top: 0;
  text-align: left;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
}

.main-headline-button {
  width: 90%;
  background-image: url("../../assets/icons/headline.svg");
  background-size: cover;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  padding: 22px;
  font-weight: 600;
  display: flex;
  font-size: 16px;
  background-color: rgba(255, 255, 255, 0);
  margin-left: auto;
}

.main-table-container {
  width: 100%;
}

.main-table-row {
  display: flex;
}

.main-table-header {
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 10px;
  border-top: 1.5px solid black;
  border-bottom: 1.5px solid black;
  padding-top: px;
  padding-bottom: 5px;
  text-align: left;
  margin-top: 50px;
  margin-bottom: 20px;
  width: 100%;
}

.main-table-cell {
  flex: 1;
}

.main-table-image {
  width: 100%;
  height: auto;
  cursor: pointer;
}

.main-table-text {
  font-weight: bold;
  text-align: left;
  width: 100%;
  font-size: 17px;
  margin-top: 10px;
  cursor: pointer;
}

.main-table-subtext {
  text-align: left;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  margin-top: 0px;
  margin-bottom: 50px;
}

.main-pencil-icon-left {
  width: 20px;
  height: 20px;
  margin-right: 60px;
  cursor: pointer;
}

.main-pencil-icon {
  width: 20px;
  height: 20px;
  margin-right: 8px;
  cursor: pointer;
}

.main-question-icon {
  width: 22px;
  height: 22px;
  margin-right: 6px;
  margin-left: 10px;
  padding-top: 5px;
}

.main-fixed-size-image {
  cursor: pointer;
  width: 305px;
  height: 200px;
  border-radius: 15px;
  object-fit: cover;
}

.main-news-button {
  border-radius: 5px;
  padding: 5px 17px;
  cursor: pointer;
  border: none;
  background-color: white;
  color: black;
  margin-bottom: 30px;
  width: 110px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3);
}

.main-news-icon {
  max-width: 90%;
  max-height: 90%;
  width: 90%;
  height: 90%;
}

.main-HAD {
  text-align: left;
  font-weight: 600;
  margin-top: 0px;
  margin-bottom: 10px;
  margin-left: 70px;
}

.main-question-icon {
  margin-right: 10px;
}

.main-table-text-container {
  display: flex;
  align-items: center;
}

.main-table-text {
  font-weight: bold;
  font-size: 17px;
  margin: 0;
  line-height: 1.5;
  margin-top: 5px;
}

.main-table-row-3 {
  display: flex;
  justify-content: space-between;
}

.main-table-cell-3 {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.main-table-cell-3:nth-child(2) {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main-table-cell-3:nth-child(3) {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}

.main-fixed-size-image {
  cursor: pointer;
  width: 305px;
  height: 200px;
  border-radius: 15px;
  object-fit: cover;
  margin-bottom: 10px;
}

.main-table-text,
.main-table-subtext,
.main-table-text-container {
  width: 305px;
  text-align: left;
}

.main-table-subtext {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  margin-top: 0;
  margin-bottom: 50px;
}

.main-button {
  width: 100%;
  padding: 16px;
  margin-top: -260px;
  background-color: #5b5b5b;
  color: white;
  font-weight: bold;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
  max-width: 305px;
  border-radius: 30px;
}

.main-news-button {
  border-radius: 5px;
  padding: 5px 17px;
  cursor: pointer;
  border: none;
  background-color: white;
  color: black;
  margin-bottom: 30px;
  width: 110px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3);
}

.main-disabled-button {
  cursor: default;
  opacity: 0;
  box-shadow: none;
  width: 110px;
  padding: 5px 17px;
  height: 35px;
  margin-bottom: 30px;
  justify-content: center;
  align-items: center;
}

.main-news-icon {
  max-width: 90%;
  max-height: 90%;
  width: 90%;
  height: 90%;
}
